import React, { Component, useEffect, useState } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Input,
  Typography,
  Badge,
  Divider,
  Tag,
} from "antd";
import { LoggedinHeader } from "../../containers";
import "./hotlineMessages.scss";
import AmWebSidebar from "./AmWebSidebar";
import FilterPopover from "./FilterPopover";
import axios from "axios";
import moment from "moment";
import authHeader from "../../services/auth-header";
import Messages from "./Messages";
import { BASE_URL } from "../../userconfig";
import { Scrollbars } from "react-custom-scrollbars";
import * as AppStorage from "../../utils/AppStorage";

const { Paragraph } = Typography;
const { Search } = Input;
const { Content } = Layout;

const HotlineMessages = () => {
  // constructor() {
  //   super();
  //   this.state = {
  //     conversations: [],
  //     conversation: "",
  //     urgencyLevel: "",
  //     status: "",
  //     searchString: "",
  //   };
  //   this.onConversationClick = this.onConversationClick.bind(this);
  //   this.onSearch = this.onSearch.bind(this);
  // }
  const [urgencyLevelData, setUrgencyLevelData] = useState([]);
  const [urgencyLevelHotlines, setUrgencyLevelHotline] = useState([]);
  const [urgencyLevelStatus, setUrgencyLevelStatus] = useState([]);
  const [conversations, setConversations] = useState([]);
  const [conversation, setConversation] = useState("");
  const [urgencyLevel, setUrgencyLevel] = useState("");
  const [status, setStatus] = useState("");
  const [searchString, setSearchString] = useState("");

  const [form] = Form.useForm();

  useEffect(() => {
    getConversations();
  }, []);
  function arrayUnique(arr) {
    const urgencyLevelTags = new Set();
    const urgencyLevelStatus = new Set();
    const urgencyLevelHotline = new Set();
    arr.forEach((item)=> {
      urgencyLevelStatus.add(item.hotline.name)
      urgencyLevelTags.add(item.urgency_level.urgency_level)
      urgencyLevelHotline.add(item.status.status)
    });
    const urgencyLevelDataArray = Array.from(urgencyLevelTags)
    const urgencyLevelStatusArray = Array.from(urgencyLevelStatus)
    const urgencyLevelHotArray = Array.from(urgencyLevelHotline)
    setUrgencyLevelData(urgencyLevelDataArray);
    setUrgencyLevelHotline(urgencyLevelHotArray);
    setUrgencyLevelStatus(urgencyLevelStatusArray);

    // const tags = [];
    // tags.push({urgency_level: Array.from(urgencyLevelTags) });
    // tags.push({status: Array.from(urgencyLevelStatus) });
    // tags.push({Array.from(urgencyLevelHotline)
  }

  const loadFilteredConversations = (data, type) => {
    const dataValue =  arrayUnique(data)
    setConversations(data);
    if (type === "reset") {
      form.setFieldsValue({ search: "" });
    }
  };
  function handleUrgencyLevel() {
    loadFilteredConversations(urgency, 'apply')
  }

  const getConversations = () => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (user)
      axios
        .get(
          BASE_URL +
            "hotlineResponderConversation/?user_id=" +
            user.user_details.org_user_id,
          options
        )
        .then((res) => {
          setConversations(res.data);
        });
  };

  const onConversationClick = (conversation) => {
    setConversation(conversation);
    setUrgencyLevel(conversation.urgency_level.urgency_level_id);
    setStatus(conversation.status.status_id);
    // this.setState({
    //   conversation: conversation,
    //   urgencyLevel: conversation.urgency_level.urgency_level_id,
    //   status: conversation.status.status_id,
    // });
  };

  const onChangeUrgencyLevel = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      urgency_level_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesUrgencyLevel/ ", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setUrgencyLevel(event);
        }
      });
  };

  const onChangeStatus = (event, conversation_id) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const data = {
      conversation_id: conversation_id,
      updated_by: user.user_details.org_user_id,
      status_id: event,
    };

    axios
      .put(BASE_URL + "hotlineConvoMessagesStatus/", data, options)
      .then((res) => {
        if (res.status === 200) {
          getConversations();
          setStatus(event);
        }
      });
  };

  const onSearch = (e) => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .get(
        BASE_URL +
          "hotlineResponderConversation/?user_id=" +
          user.user_details.org_user_id +
          "&search=" +
          e,
        options
      )
      .then((res) => {
        setConversations(res.data);
        setSearchString(e);
        // this.setState({
        //   conversations: res.data,
        //   searchString: e,
        // });
      });
  };

  return (
    <>
      <Row className="inner-header">
        <LoggedinHeader amWEBUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <AmWebSidebar amWEBUser />
            <Content className="">
              <Row className="py-3">
                <h1 className="mb-0">Hotline Messages</h1>
              </Row>
              <div className="mid-content p-0">
                <Row>
                  <Col span={6} className="message-section py-4">
                    <Col
                      span={24}
                      className=" d-flex justify-content-between px-3"
                    >
                      <h6>Messages</h6>
                      <FilterPopover
                        searchString={searchString}
                        loadFilteredConversations={loadFilteredConversations}
                      />
                    </Col>

                    <Col span={24} className="mt-4 px-3 d-block">
                      {urgencyLevelData.map((data) => (
                        <>
                         <Tag  onChange={handleUrgencyLevel} className="mb-2">
                          Urgency : <span>{data}</span>
                        </Tag> 
                        </>
                      ))}
                      {console.log('....', urgencyLevelHotlines)}
                      {urgencyLevelHotlines.map((hotline) => {
                        <>
                        <Tag  className="mb-2">
                        Hotline :{" "}
                          <span>{hotline} </span>
                        </Tag> 
                        </>
                      })}
                      {urgencyLevelStatus.map((status) => {
                        <>
                        <Tag  className="mb-2">
                          Status :{" "}
                          <span>{status} </span>
                        </Tag> 
                        </>
                      })}

                    </Col>
                    <Col span={24} className="search-box mt-3 px-3">
                      <Form form={form}>
                        <Form.Item name="search" id="search">
                          <Search
                            name="search"
                            id="search"
                            placeholder="Search"
                            onSearch={onSearch}
                          />
                        </Form.Item>
                      </Form>
                    </Col>
                    <Scrollbars
                      // onScroll={handleScroll}
                      // onScrollFrame={handleScrollFrame}
                      // onScrollStart={handleScrollStart}
                      // onScrollStop={handleScrollStop}
                      // onUpdate={handleUpdate}
                      // renderView={renderView}
                      // renderTrackHorizontal={renderTrackHorizontal}
                      // renderTrackVertical={renderTrackVertical}
                      // renderThumbHorizontal={renderThumbHorizontal}
                      // renderThumbVertical={renderThumbVertical}
                      autoHeight
                      autoHeightMin={0}
                      autoHeightMax={450}
                      thumbMinSize={30}
                      universal={true}
                    >
                      <Col span={24} className="mt-4 inbox-messages">
                        {conversations.length > 0 ? (
                          conversations.map((item) => (
                            <div
                              className="msg-wrapper"
                              onClick={() => onConversationClick(item)}
                            >
                              <div className="d-flex justify-content-between">
                                <Paragraph ellipsis className="w-75 pr-2 mb-1">
                                  {item.responder_conversation_name}
                                </Paragraph>

                                <span className="small">
                                  {" "}
                                  {moment(item.created_on).isSame(
                                    moment(),
                                    "day"
                                  )
                                    ? moment(item.created_on).format("hh:mm")
                                    : moment(item.created_on).format("MM/DD")}
                                </span>
                              </div>
                              <div className="d-flex justify-content-between">
                                <Paragraph
                                  ellipsis
                                  className="font-weight-normal mb-1 sm"
                                >
                                  {item.hotline.name} | {item.conversation_id}
                                </Paragraph>
                                <span className="small">
                                  {" "}
                                  <Badge count={item.unread_count} />
                                </span>
                              </div>
                            </div>
                          ))
                        ) : (
                          <div className="msg-wrapper">No messages found</div>
                        )}
                      </Col>
                    </Scrollbars>
                  </Col>

                  {conversation && (
                    <Messages
                      conversation={conversation}
                      urgencyLevel={urgencyLevel}
                      status={status}
                      onChangeUrgencyLevel={onChangeUrgencyLevel}
                      onChangeStatus={onChangeStatus}
                    />
                  )}
                </Row>
              </div>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </>
  );
};

export default HotlineMessages;